@import 'common/variables';

.wrapper {
  display: flex;
  justify-content: space-around;
  margin: 10px;

  .card {
    width: 150px;
    height: 150px;
    padding: 2rem 1rem;
    background: transparent;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    box-shadow: 0 7px 10px rgba(0, 0, 0, 0.5);
    transition: 0.5s ease-in-out;
    border-radius: 20px;

    &:hover {
      transform: translateY(20px);
    }

    &:before {
      border-radius: 20px;
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 100%;
      background: linear-gradient(to bottom, rgba(145, 159, 158, 0.5), rgb(215, 245, 68));
      z-index: 2;
      transition: 0.5s all;
      opacity: 0;
    }

    &:hover:before {
      opacity: 1;
    }

    & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
    }

    & .info {
      position: relative;
      z-index: 3;
      color: #fff;
      opacity: 0;
      transform: translateY(30px);
      transition: 0.5s all;
    }

    &:hover .info {
      opacity: 1;
      transform: translateY(0px);
    }

    & .info h3 {
      text-align: center;
    }

    & .info p {
      letter-spacing: 1px;
      font-size: 15px;
      margin-top: 8px;
      margin-bottom: 20px;
    }

  }


}